import { movies } from "./getmovies";
import React, { Component } from 'react'

export default class banner extends Component {
    render() {
        let movie = movies.results[0];
        let movie1 = movies.results[1];
        let movie2 = movies.results[2];
        return (
            <div id="myCarousel" class="carousel slide slider-banner" data-bs-ride="carousel">
    <div class="carousel-indicators">
      <button type="button" data-bs-target="#myCarousel" data-bs-slide-to="0" class="active" aria-label="Slide 1" aria-current="true"></button>
      <button type="button" data-bs-target="#myCarousel" data-bs-slide-to="1" aria-label="Slide 2" class=""></button>
      <button type="button" data-bs-target="#myCarousel" data-bs-slide-to="2" aria-label="Slide 3" class=""></button>
    </div>
    <div class="carousel-inner">
      <div class="carousel-item active">
        {/* <svg class="bd-placeholder-img" width="100%" height="100%" xmlns="http://www.w3.org/2000/svg" aria-hidden="true" preserveAspectRatio="xMidYMid slice" focusable="false"><rect width="100%" height="100%" fill="#777"></rect></svg> */}
        <img class="bd-placeholder-img" width="100%" src={`https://image.tmdb.org/t/p/original${movie.backdrop_path}`} aria-hidden="true" preserveAspectRatio="xMidYMid slice" focusable="false" />

        <div class="container">
          <div class="carousel-caption">
          <h1>{movie.original_title}</h1>
          {/* <p>{movie.overview}</p> */}
          </div>
        </div>
      </div>
      <div class="carousel-item">
        <img class="bd-placeholder-img" width="100%" src={`https://image.tmdb.org/t/p/original${movie1.backdrop_path}`} aria-hidden="true" preserveAspectRatio="xMidYMid slice" focusable="false" />

        <div class="container">
          <div class="carousel-caption">
          <h1>{movie1.original_title}</h1>
          {/* <p>{movie1.overview}</p> */}
          </div>
        </div>
      </div>
      <div class="carousel-item">
        <img class="bd-placeholder-img" width="100%" src={`https://image.tmdb.org/t/p/original${movie2.backdrop_path}`} aria-hidden="true" preserveAspectRatio="xMidYMid slice" focusable="false" />

        <div class="container">
          <div class="carousel-caption ">
          <h1>{movie2.original_title}</h1>
          {/* <p>{movie2.overview}</p> */}
          </div>
        </div>
      </div>
    </div>
    <button class="carousel-control-prev" type="button" data-bs-target="#myCarousel" data-bs-slide="prev">
      <span class="carousel-control-prev-icon" aria-hidden="true"></span>
      <span class="visually-hidden">Previous</span>
    </button>
    <button class="carousel-control-next" type="button" data-bs-target="#myCarousel" data-bs-slide="next">
      <span class="carousel-control-next-icon" aria-hidden="true"></span>
      <span class="visually-hidden">Next</span>
    </button>
  </div>
        )
    }
}